import logo from './logo.svg';
import React, {useState, useEffect} from "react";
import axios from "axios";
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';

function App() {
    let [liveEvents, setLiveEvents] = useState([])
    useEffect(() => {
        axios({
            method: "get",
            url: '/api/live_events/',
        }).then(response => {
            setLiveEvents(response.data);
        })
    })
    console.log(liveEvents)
    const baseFactors = ['1', 'X', '2', '1X', 'X2', '12', "Ф1", "Ф2", 'ТБ', 'ТМ', 'Всего']

    return (
        <div className="col-md-12 col-lg-11 wrapper">
            <h1>Текущие события:</h1>
            <div className="events-table">
                <div className="table-header">
                    <div className="line row">
                        <div className="col-md-12 col-lg-7 event-name"><h6>Событий найдено: {liveEvents.length}</h6></div>
                        <div className="col-md-12 col-lg-5 counters">
                            {baseFactors.map(factor => (
                                <div className="counter">{factor}</div>
                            ))}
                        </div>
                    </div>
                </div>
                <div className="table-body">
                    {liveEvents.map(event => (
                        <div className="line row" key={event.id}>
                            <div className={"col-md-12 col-lg-7 event-name level-" + event.level}><h6>{event.name}</h6>
                            </div>
                            <div className="col-md-12 col-lg-5 counters">
                                {event.counters.map(c => (
                                    <div className="counter"><p>{c.value}</p></div>
                                ))}
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
}

export default App;
